<h2 id="learning-activities" tabindex="-1"><a class="header-anchor" href="#learning-activities" aria-hidden="true">#</a> Learning Activities</h2>
<p>Here are short introduction to the learning activities offered in the course.</p>
<h3 id="recorded-lectures" tabindex="-1"><a class="header-anchor" href="#recorded-lectures" aria-hidden="true">#</a> Recorded Lectures</h3>
<p>In the beginning of some weeks you will be encouraged to view some pre-recorded lectures (or read some text). These will be quite theoretical and will teach you new things you can use when building a website.</p>
<h3 id="tutorials" tabindex="-1"><a class="header-anchor" href="#tutorials" aria-hidden="true">#</a> Tutorials</h3>
<p>Some weeks there will be a tutorial session where the teacher will show you how to use what has been taught in the pre-recorded lectures in practice.</p>
<h3 id="lab-sessions" tabindex="-1"><a class="header-anchor" href="#lab-sessions" aria-hidden="true">#</a> Lab Sessions</h3>
<p>Each week you may attend a four hours long lab session. Some of the lab sessions have been scheduled in ordinary classrooms, and for those sessions you have to bring your own laptop to work on. If you can't do that, make sure to come to a lab session scheduled in a computer room.</p>
<p>Select which lab session you want to attend by joining the corresponding <a href="https://ju.instructure.com/courses/4775/groups" target="_blank" rel="noopener noreferrer">Lab Session Group<OutboundLink/></a>. Feel free to show up at and attend other lab sessions too <strong>if there's room for it</strong>, but you may only join one Lab Session Group on Canvas.</p>
<h2 id="examination-tests" tabindex="-1"><a class="header-anchor" href="#examination-tests" aria-hidden="true">#</a> Examination Tests</h2>
<p>The course contains three examination tests.</p>
<h3 id="_1-written-examination" tabindex="-1"><a class="header-anchor" href="#_1-written-examination" aria-hidden="true">#</a> 1. Written Examination</h3>
<p>2.5 credits, graded <code>Fail</code>, <code>3</code>, <code>4</code>, and <code>5</code>. More information is available on the <RouterLink to="/courses/web-development-advanced-concepts/written-examination/">Written Examination</RouterLink> page.</p>
<h3 id="_2-laboratory-work" tabindex="-1"><a class="header-anchor" href="#_2-laboratory-work" aria-hidden="true">#</a> 2. Laboratory Work</h3>
<p>1 credit, graded <code>Fail</code> and <code>Pass</code>. More information is available on the <RouterLink to="/courses/web-development-advanced-concepts/laboratory-work/">Laboratory Work</RouterLink> page.</p>
<h3 id="_3-project-work" tabindex="-1"><a class="header-anchor" href="#_3-project-work" aria-hidden="true">#</a> 3. Project Work</h3>
<p>4 credits, graded <code>Fail</code>, <code>3</code>, <code>4</code> and <code>5</code>. More information is available on the <RouterLink to="/courses/web-development-advanced-concepts/project-work/">Project Work</RouterLink> page.</p>
<h3 id="re-examination" tabindex="-1"><a class="header-anchor" href="#re-examination" aria-hidden="true">#</a> Re-examination</h3>
<p><FigureNumber /> below shows the study and exam periods during a year. The small yellow circle sectors shows when the (re-)examinations are offered in this course (at the re-exam periods in June and August).</p>
<AcademicYearFigure />
<p>If you need to know a precise date and time for the re-examination in advance, email the course coordinator at <code>peter.larsson-green@ju.se</code>. An announcement on Canvas with more information will be posted soon before the re-examination periods for this course.</p>
<p>The project work will only be graded 3/U (U=fail) at re-examination.</p>
<p>If you haven't passed some examination tests after the three examination opportunities (ordinary + 2 re-exam) you need to re-take those tests the next time the course run. Note that the instructions for those tests might have changed the next time the course run, and that you should take them according to the new instructions.</p>
<h3 id="final-grade" tabindex="-1"><a class="header-anchor" href="#final-grade" aria-hidden="true">#</a> Final grade</h3>
<p>To receive a final grade in the course you need to pass all examination tests. The final grade will be based on the lowest grade of the <code>Project Work</code> and the <code>Written Examination</code>, but these grades will also be marked as <em>weak</em> or <em>strong</em>. If one of the grades is lower than the other and has been marked as <em>strong</em>, the final grade will be the lower grade + 1. For example, a strong 3 and a weak 4 gives you 4 as the final grade, while a weak 3 and a strong 5 gives you 3 as the final grade. For the <code>Written Examination</code>, 50% of max score is a strong 3, and 70% of max score is a strong 4.</p>
